<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import { dataTableService } from "@/common/datatable-mixin.js";
import ZohoSearch from "@/components/ZohoSearch.vue";
import ReceiptOne from "@/index/shared/ReceiptOne.vue";
export default {
  name: "SalesReport",
  mixins: [dataTableService],
  components: { ContentHeader, ZohoSearch, ReceiptOne },
  data: () => ({
    selectedRow: null,
  }),
  computed: {
    isSalesReport() {
      return this.$route.name == "SalesReport";
    },
  },
  created() {
    this.fetchUrl = this.isSalesReport
      ? "reports/sales"
      : "reports/sales?category=credited";
    this.handlePaginationSearch();
  },
  methods: {
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`).then((response) => {
        if (response.data.status) {
          let index = this.rows.findIndex((item) => item.id == orderId);
          this.rows.splice(index, 1);
          this.$store.commit("SET_FLASH_MESSAGE", {
            type: "success",
            message: "Sales deleted successfully",
          });
        }
      });
    },
    handleClientFilter(client) {
      if (client) {
        this.newFilter.client = client.id;
        this.handlePaginationFilter();
      }
    },
    handleWaiterFilter(waiter) {
      if (waiter) {
        this.newFilter.waiter = waiter.id;
        this.handlePaginationFilter();
      }
    },
    selectRow(row) {
      this.selectedRow = row;
      this.toggleModal();
    },
  },
};
</script>
<template>
  <content-header
    :title="`${isSalesReport ? 'Sales' : 'Credit Post'} Report`"
    subtitle="Explore report and it details throughout period of time"
  ></content-header>
  <div class="content__boxed">
    <div class="content__wrap">
      <div class="card">
        <div class="card-header">
          <div class="row">
            <div class="col-md-6 d-flex gap-1 align-items-center mb-1">
              <zoho-search
                url="clients/search"
                label="name"
                primary-key="id"
                placeholder="select a client"
                @selected="handleClientFilter"
                :load-defaults="true"
                :records="
                  isSalesReport
                    ? [{ id: 'walk-in', name: 'Walk-in Customer' }]
                    : []
                "
                fetch-flag="SEARCHING_FOR_CLIENTS"
              ></zoho-search>
              <div class="ms-1">
                <zoho-search
                  url="users/search?role=waiters"
                  label="name"
                  primary-key="id"
                  placeholder="select a waiter"
                  @selected="handleWaiterFilter"
                  :load-defaults="true"
                  fetch-flag="SEARCHING_FOR_WAITERS"
                ></zoho-search>
              </div>
            </div>
            <div
              class="col-md-6 d-flex gap-1 align-items-center justify-content-md-end mb-1"
            >
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-printer fs-5"></i>
              </button>
              <button class="btn btn-icon btn-outline-light">
                <i class="demo-pli-file-excel fs-5"></i>
              </button>
              <div class="filters">
                <button
                  class="btn btn-icon btn-outline-light"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <i class="fs-5 demo-pli-calendar-4"></i>
                </button>
                <div
                  class="dropdown-menu dropdown-menu-end w-md-200px"
                  style=""
                >
                  <div class="form-group mb-2">
                    <label for="from-date">From:</label>
                    <input
                      type="date"
                      class="form-control"
                      v-model="newFilter.from"
                      placeholder="Start date"
                    />
                  </div>
                  <div class="form-group mb-2">
                    <label for="from-date">To:</label>
                    <input
                      type="date"
                      class="form-control"
                      v-model="newFilter.to"
                      placeholder="End date"
                    />
                  </div>
                  <div class="form-group">
                    <button
                      class="btn w-100 btn-primary"
                      type="button"
                      @click="handlePaginationFilter"
                    >
                      Filter
                    </button>
                  </div>
                </div>
              </div>
              <div class="filters" v-if="false">
                <button
                  class="btn btn-outline-light"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <svg
                    height="20px"
                    viewBox="0 0 16 16"
                    xmlns="http://www.w3.org/2000/svg"
                    fill="currentColor"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M8 16A8 8 0 1 0 8 0a8 8 0 0 0 0 16zM3.5 5a.5.5 0 0 0 0 1h9a.5.5 0 0 0 0-1h-9zM5 8.5a.5.5 0 0 1 .5-.5h5a.5.5 0 0 1 0 1h-5a.5.5 0 0 1-.5-.5zm2 3a.5.5 0 0 1 .5-.5h1a.5.5 0 0 1 0 1h-1a.5.5 0 0 1-.5-.5z"
                    ></path>
                  </svg>
                  <span class="text-dark fw-bold ms-1">Filter</span>
                </button>
                <ul class="dropdown-menu dropdown-menu-end" style="">
                  <li>
                    <a class="dropdown-item" href="javascript:void(0)"
                      >All sales</a
                    >
                  </li>
                  <li>
                    <a class="dropdown-item" href="javascript:void(0)">Paid</a>
                  </li>
                  <li>
                    <a class="dropdown-item" href="javascript:void(0)">Due</a>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
        <div class="card-body">
          <div class="table-responsive">
            <table class="table table-striped table-sm">
              <thead>
                <tr>
                  <th>Invoice #</th>
                  <th v-if="isSalesReport">Table</th>
                  <th>Waiter</th>
                  <th>Branch</th>
                  <th>Client</th>
                  <th>Amount</th>
                  <th>Paid</th>
                  <th>Balance</th>
                  <th v-if="isSalesReport">Cashier</th>
                  <th>Status</th>
                  <th>Created at</th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="order in rows" :key="order.reference">
                  <td>
                    <span>#{{ $helper.generateVoucherNo(order.id) }}</span>
                  </td>
                  <td v-if="isSalesReport">
                    <a class="btn-link" href="javascript:void(0)">{{
                      order.table.name
                    }}</a>
                  </td>
                  <td>
                    <span class="fw-bolder">
                      {{ order.waiter ? order.waiter.name : "Admin" }}</span
                    >
                  </td>
                  <td>
                    <a class="btn-link" href="javascript:void(0)">
                      {{ order.branch ? order.branch.name : "Headquarter" }}</a
                    >
                  </td>
                  <td>
                    <span>{{
                      order.client ? order.client.name : "Walk-in"
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total)
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.payment)
                    }}</span>
                  </td>
                  <td>
                    <span class="fw-bolder">{{
                      formatMoney(order.grand_total - order.payment)
                    }}</span>
                  </td>
                  <td v-if="isSalesReport">
                    <span class="fw-bolder">
                      {{ order.cashier ? order.cashier.name : "Admin" }}</span
                    >
                  </td>
                  <td>
                    <span class="badge bg-success" v-if="order.paid == 1"
                      >Paid</span
                    >
                    <span class="badge bg-warning" v-else>Due</span>
                  </td>
                  <td>
                    <span>{{
                      `${$helper.formatDate(
                        order.created_at
                      )} ${$helper.formatOrderTime(order.created_at)}`
                    }}</span>
                  </td>
                  <td class="text-end text-nowrap">
                    <div class="">
                      <button
                        class="btn btn-icon btn-xs btn-light"
                        data-bs-toggle="dropdown"
                        aria-expanded="false"
                      >
                        <i class="demo-pli-dot-horizontal"></i>
                        <span class="visually-hidden">Toggle Dropdown</span>
                      </button>
                      <ul class="dropdown-menu dropdown-menu-end" style="">
                        <li>
                          <a class="dropdown-item" href="javascript:void(0)"
                            ><svg
                              height="16px"
                              xmlns="http://www.w3.org/2000/svg"
                              viewBox="0 0 24 24"
                              fill="none"
                              stroke="currentColor"
                              stroke-width="2"
                              stroke-linecap="round"
                              stroke-linejoin="round"
                              class="me-1"
                            >
                              <path
                                d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"
                              ></path>
                              <circle cx="12" cy="12" r="3"></circle></svg
                            >View Items</a
                          >
                        </li>
                        <li>
                          <a
                            class="dropdown-item"
                            href="javascript:void(0)"
                            @click.prevent="selectRow(order)"
                          >
                            <i class="demo-pli-printer fs-5 me-1"></i> Print</a
                          >
                        </li>
                        <li>
                          <a class="dropdown-item" href="javascript:void(0)"
                            ><i class="demo-pli-pen-5 fs-5 me-1"></i>Edit</a
                          >
                        </li>
                        <li>
                          <a
                            class="dropdown-item"
                            href="javascript:void(0)"
                            @click.prevent="
                              $confirm({
                                title: 'Delete sale record',
                                content: `Are you sure you want to delete this sale #${$helper.generateVoucherNo(
                                  order.id
                                )} ?`,
                                classes: 'btn btn-danger',
                                actionText: 'Yes, Continue',
                                closeText: 'Cancel',
                                action: () => {
                                  handleDestroy(order.id);
                                },
                              })
                            "
                            ><i class="demo-pli-trash fs-5 me-1"></i>Delete</a
                          >
                        </li>
                      </ul>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <pagination
            :from="recordsFrom"
            :to="recordsTo"
            :current_page="currentPage"
            :row_count_page="rowCountPage"
            @page-update="handlePagination"
            :total_records="totalRows"
            :page_range="pageRange"
          ></pagination>
        </div>
      </div>
    </div>
  </div>
  <div
    class="receipt receipt-end border-0 shadow"
    tabindex="-1"
    style="visibility: visible"
    aria-modal="true"
    role="dialog"
    :class="{ 'show-receipt': selectedRow != null }"
  >
    <div class="receipt-header bg-dark">
      <h4 class="receipt-title">RECEIPT</h4>
      <div class="text-center text-nowrap">
        <a class="btn btn-icon btn-xs btn-primary" href="javascript:void(0)"
          ><i class="demo-pli-download-from-cloud fs-4 fw-bolder"></i
        ></a>
        <a
          class="btn btn-icon btn-xs btn-success mx-2"
          href="javascript:void(0)"
          @click.prevent="handleReceiptPrint('print-container')"
        >
          <svg
            height="18px"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              d="M17 17h2a2 2 0 002-2v-4a2 2 0 00-2-2H5a2 2 0 00-2 2v4a2 2 0 002 2h2m2 4h6a2 2 0 002-2v-4a2 2 0 00-2-2H9a2 2 0 00-2 2v4a2 2 0 002 2zm8-12V5a2 2 0 00-2-2H9a2 2 0 00-2 2v4h10z"
            ></path></svg
        ></a>
        <a
          class="btn btn-icon btn-xs btn-danger"
          href="javascript:void(0)"
          @click.prevent="selectRow(null)"
          ><i class="demo-pli-cross fs-5 fw-bolder"></i
        ></a>
      </div>
    </div>
    <div class="receipt-body scrollable-content px-1">
      <receipt-one
        v-if="selectedRow != null"
        :order="selectedRow"
      ></receipt-one>
    </div>
  </div>
</template>
