<script>
import ContentHeader from "@/index/shared/ContentHeader.vue";
import EmptyResults from "@/components/EmptyResults.vue";
import Avatar from "@/components/Avatar.vue";
import LoadingDots from "@/components/LoadingDots.vue";
export default {
  name: "Orders",
  components: { ContentHeader, EmptyResults, Avatar, LoadingDots },
  data: () => ({
    fetchUrl: "orders",
    selectedOrder: null,
    paymentMethod: "Cash",
    listingCategory: "all",
    rows: [],
    latestOrderInterval: null,
    newFilter: {
      status: "all",
      period: "all",
    },
    filters: {
      types: [
        {
          label: "All",
          value: "all",
        },
        {
          label: "On-Progress",
          value: "PENDING",
        },
        {
          label: "Completed",
          value: "DELIVERED",
        },
      ],
      periods: [
        {
          label: "All",
          value: "All",
        },
        {
          label: "Today",
          value: "Today",
        },
        {
          label: "Yesterday",
          value: "Yesterday",
        },
        {
          label: "This Week",
          value: "This Week",
        },
        {
          label: "This Month",
          value: "This Month",
        },
        {
          label: "This Year",
          value: "This Year",
        },
      ],
    },
    clients: [],
    waiters: [],
    waitersFilterKey: null,
    searchKey: null,
    clientKey: null,
    client: null,
    selectedWaiter: null,
  }),
  computed: {
    filteredRows() {
      let status = this.newFilter.status != "all" && this.newFilter.status;
      let rows = this.rows;
      if (status) {
        rows = rows.filter((row) => row.status == status);
      }
      if (this.selectedWaiter) {
        rows = rows.filter((row) => row.waiter_id == this.selectedWaiter.id);
      }
      return rows;
    },
    submitDisabled() {
      return false;
    },
    filteredWaiters() {
      let keyword =
        this.waitersFilterKey && this.waitersFilterKey.toLowerCase();
      let waiters = this.waiters;
      if (keyword) {
        waiters = waiters.filter(
          (item) => String(item.name).toLowerCase().indexOf(keyword) > -1
        );
      }
      return waiters;
    },
    filteredClients() {
      let keyword = this.clientKey && this.clientKey.toLowerCase();
      let clients = this.clients;
      if (keyword) {
        clients = clients.filter(
          (item) => String(item.name).toLowerCase().indexOf(keyword) > -1
        );
      }
      return clients;
    },
    hasKeyword() {
      return this.searchKey != null && this.searchKey.trim().length >= 2;
    },
  },
  created() {
    this.resetFilters();
    this.$http.get(this.fetchUrl).then((response) => {
      this.rows = response.data.rows;
      this.fetchUrl = `orders?latest=${response.data.latest}`;
      this.getLatestOrder();
    });
  },
  methods: {
    handleWaiterFilter(waiter) {
      this.selectedWaiter = waiter;
    },
    resetFilters() {
      this.newFilter.status = this.filters.types[0].value;
      this.newFilter.period = this.filters.periods[0].value;
    },
    getLatestOrder() {
      this.latestOrderInterval = setInterval(() => {
        this.$store.commit("SET_REQUEST_FLAG", "GETTING_LATEST_ORDERS");
        this.$http.get(this.fetchUrl).then((response) => {
          if (response.data.rows.length) {
            this.rows = [...response.data.rows, ...this.rows];
            this.fetchUrl = `orders?latest=${response.data.latest}`;
          }
        });
      }, 3000);
    },
    selectOrder(order) {
      if (order) {
        document.querySelector("#root").classList.add("sb--show");
      } else {
        document.querySelector("#root").classList.remove("sb--show");
      }
      this.selectedOrder = order;
    },
    closeSidebar() {
      document.querySelector("#root").classList.remove("sb--show");
      this.selectedOrder = null;
    },
    handlePayment() {
      this.$http
        .post(
          "orders/payment",
          this.$helper.generateFormData({
            order: this.selectedOrder.id,
            payment_method: this.paymentMethod,
            discount: 0,
          })
        )
        .then((response) => {
          if (response.data.status) {
            let index = this.rows.findIndex(
              (item) => item.id == this.selectedOrder.id
            );
            this.rows.splice(index, 1);
            this.closeSidebar();
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: `Payment to order ${this.selectedOrder.reference} has been made sucessfully`,
            });
          }
        });
    },
    handleSearch(fetchUrl = null) {
      let url = fetchUrl;
      if (this.hasKeyword) {
        this.$store.commit("SET_REQUEST_FLAG", "PAGINATION_SEARCH");
        if (url.indexOf("?") > -1)
          url += `&q=${this.filterKey}&per_page=${this.rowCountPage}`;
        else url += `?q=${this.filterKey}&per_page=${this.rowCountPage}`;
      }
      this.$http.get(url).then((response) => {
        this.rows = response.data.rows;
      });
    },
    handleFilter() {
      let fetchUrl = this.$helper.encodeQuery("orders", this.newFilter);
      this.$store.commit("SET_REQUEST_FLAG", "PAGINATION_SEARCH");
      this.handleSearch(fetchUrl);
    },
    handleDestroy(orderId) {
      this.$http.get(`orders/delete/${orderId}`)
        .then((response) => {
          if (response.data.status) {
            let index = this.rows.findIndex((item) => item.id == orderId);
            this.rows.splice(index, 1);
            this.closeSidebar();
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Order deleted successfully",
            });
          }
        });
    },
    getClients() {
      if (!this.clients.length) {
        this.$store.commit("SET_REQUEST_FLAG", "GETTING_CLIENTS");
        this.$http.get("clients/search")
          .then((response) => {
            this.clients = response.data;
          });
      }
    },
    assignClient() {
      this.$confirm({
        title: "Credit Post",
        content: `Are you sure you want to assign this order #${this.$helper.generateVoucherNo(
          this.selectedOrder.id
        )} to ${this.client.name}?`,
        classes: "btn btn-primary",
        actionText: "Yes, Continue",
        closeText: "Cancel",
        action: () => {
          this.$http
            .post(
              "orders/unpaid/assign",
              this.$helper.generateFormData({
                client_id: this.client.id,
                order_id: this.selectedOrder.id,
              })
            )
            .then((response) => {
              if (response.data.status) {
                let index = this.rows.findIndex(
                  (item) => item.id == this.selectedOrder.id
                );
                this.rows.splice(index, 1);
                this.closeSidebar();
                this.$store.commit("SET_FLASH_MESSAGE", {
                  type: "success",
                  message: "Credit posted successfully",
                });
              }
            });
        },
      });
    },
  },
  beforeUnmount() {
    clearInterval(this.latestOrderInterval);
  },
  mounted() {
    this.$store.commit("SET_REQUEST_FLAG", "GET_FILTERS_RESOURCES");
    this.$http.get("users").then((response) => {
      this.waiters = response.data.users;
    });
    /*this.$http.all([this.$http.get("users")]).then(
      this.$http.spread((responseTwo) => {
        
      })
    ); */
  },
};
</script>
<template>
  <content-header
    v-if="false"
    title="Orders List"
    subtitle="View All orders that are in progress or completed"
    classes="custom"
  ></content-header>
  <div class="container-fluid mt-2">
    <div class="row">
      <div class="pt-1 col-lg-12">
        <div class="d-flex align-items-center">
          <div class="d-flex align-items-center flex-nowrap">
            <span class="h6 mb-0 me-1">Filter By:</span>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-light border bg-white rounded-1 dropdown-toggle"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Type: <span class="fw-bolder">{{ newFilter.status }}</span>
              </button>
              <ul class="dropdown-menu data-filters" style="">
                <li v-for="(item, i) in filters.types" :key="'order-types' + i">
                  <a
                    class="dropdown-item"
                    @click.prevent="newFilter.status = item.value"
                    :class="{ active: newFilter.status == item.value }"
                    href="javascript:void(0)"
                    >{{ item.label }}</a
                  >
                </li>
              </ul>
            </div>
            <div class="btn-group">
              <button
                type="button"
                class="btn btn-light border bg-white ms-1 rounded-1 dropdown-toggle"
                data-bs-toggle="dropdown"
                aria-expanded="false"
              >
                Period : <span class="fw-bolder">{{ newFilter.period }}</span>
              </button>
              <div class="dropdown-menu">
                <div :class="{ 'd-flex': newFilter.period == 'custom' }">
                  <ul class="ranges data-filters" style="">
                    <li
                      v-for="(item, i) in filters.periods"
                      :key="'periods' + i"
                    >
                      <a
                        class="dropdown-item"
                        @click.prevent="newFilter.period = item.value"
                        :class="{ active: newFilter.period == item.value }"
                        href="javascript:void(0)"
                        >{{ item.label }}</a
                      >
                    </li>
                    <li>
                      <a
                        class="dropdown-item"
                        href="javascript:void(0)"
                        @click.prevent="newFilter.period = 'custom'"
                        :class="{ active: newFilter.period == 'custom' }"
                        >Custom</a
                      >
                    </li>
                  </ul>
                  <div
                    class="custom-range border-start d-block ps-2"
                    v-if="newFilter.period == 'custom'"
                  >
                    <div class="custom-ranger-wraper">
                      <div class="date-picker">
                        <div class="form-group mb-2">
                          <label for="">From:</label>
                          <input
                            type="date"
                            name=""
                            id=""
                            class="form-control"
                          />
                        </div>
                        <div class="form-group">
                          <label for="">To:</label>
                          <input
                            type="date"
                            name=""
                            id=""
                            class="form-control"
                          />
                        </div>
                      </div>
                      <div class="bottom-0 text-end">
                        <button class="btn btn-sm btn-primary" type="button">
                          Apply
                        </button>
                        <button class="btn btn-sm btn-light ms-1" type="button">
                          Cancel
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <button
              class="btn btn-icon btn-light bg-white"
              type="button"
              @click="reloadPage"
            >
              <svg
                height="18px"
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-width="2"
                  d="M4 4v5h.582m15.356 2A8.001 8.001 0 004.582 9m0 0H9m11 11v-5h-.581m0 0a8.003 8.003 0 01-15.357-2m15.357 2H15"
                ></path>
              </svg>
            </button>
          </div>
          <div class="ms-auto">
            <div class="d-flex flex-nowrap align-items-center">
              <div class="form-group">
                <input
                  type="text"
                  placeholder="Search by Order ID..."
                  class="form-control"
                  autocomplete="off"
                />
              </div>
              <!-- Start of Waiters Filter -->
              <div class="icon">
                <div class="dropdown">
                  <a
                    href="javascript:void(0)"
                    data-bs-toggle="dropdown"
                    class="btn btn-icon btn-light bg-white"
                  >
                    <img
                      src="/img/waiter.png"
                      alt="waiter"
                      style="width: 18px"
                    />
                  </a>
                  <div
                    class="dropdown-menu dropdown-menu-end w-md-200px w-sm-250px"
                    data-popper-placement="bottom-end"
                  >
                    <div class="border-bottom px-1 py-2">
                      <div class="form-group">
                        <input
                          type="search"
                          placeholder="Search for a waiter"
                          class="form-control"
                          autocomplete="off"
                          v-model="waitersFilterKey"
                        />
                      </div>
                    </div>

                    <div
                      class="list-group list-group-borderless listgroup-search scrollable-content"
                    >
                      <div
                        v-for="waiter in filteredWaiters"
                        :key="waiter.id"
                        class="list-group-item list-group-item-action d-flex align-items-start mb-2"
                        :class="{
                          'selected-option':
                            selectedWaiter && selectedWaiter.id == waiter.id,
                        }"
                        @click="handleWaiterFilter(waiter)"
                      >
                        <div class="flex-shrink-0 me-2">
                          <avatar
                            :name="waiter.name"
                            width="28px"
                            height="28px"
                          ></avatar>
                        </div>
                        <div class="flex-grow-1">
                          <a
                            href="javascript:void(0)"
                            class="h6 d-block mb-0 stretched-link text-decoration-none"
                            >{{ waiter.name }}</a
                          >
                          <small class="text-muted">
                            @{{
                              waiter.name.toLowerCase().split(" ").join("_")
                            }}</small
                          >
                        </div>
                      </div>
                      <span
                        class="py-2 text-center"
                        v-if="!filteredWaiters.length"
                        >No waiters found</span
                      >
                    </div>
                  </div>
                </div>
              </div>
              <!-- END --->
            </div>
          </div>
        </div>
        <div
          class="d-flex justify-content-center"
          style="min-height: 65vh"
          v-if="!filteredRows.length"
        >
          <empty-results>
            <template #description
              ><span
                >No orders found. Please wait for new orders</span
              ></template
            >
          </empty-results>
        </div>
        <div class="main-side scrollable-content w-100 pt-3" v-else>
          <div class="order-wraper d-grid">
            <a
              href="javascript:void(0)"
              class="rounded-2 box-shadow bg-white border p-2 order-list-item mb-2 position-relative"
              v-for="(order, i) in filteredRows"
              :key="'order' + i"
              @click.prevent="selectOrder(order)"
              :class="{ active: selectedOrder && selectedOrder.id == order.id }"
            >
              <span
                class="badge bg-success rounded-circle completed-order p-1"
                v-if="order.printed == 1"
              >
                <i class="demo-pli-yes fs-5"></i>
              </span>
              <div class="d-flex align-items-center flex-nowrap">
                <div class="order-left">
                  <h5 class="mb-0 fw-bolder">
                    Order: #{{ $helper.generateVoucherNo(order.id) }}
                  </h5>
                  <a href="javascript:void(0)" class="m-0 h6">{{
                    order.table.name
                  }}</a>
                  <h6 class="m-0 text-muted">
                    By:
                    {{ order.waiter ? order.waiter.name : "Administrator" }}
                  </h6>
                  <span class="text-primary">Qta {{ order.items.length }}</span>
                </div>
                <div class="ms-auto text-end">
                  <p class="m-0 text-dark">
                    {{ $helper.formatOrderDate(order.created_at) }}
                  </p>
                  <p class="mb-0 text-muted">
                    {{ $helper.formatOrderTime(order.created_at) }}
                  </p>
                  <div class="d-flex flex-nowrap">
                    <h5 class="m-0">
                      <span class="fw-bolder text-dark">{{
                        formatMoney(order.grand_total)
                      }}</span>
                      <span class="badge bg-secondary ms-2">{{
                        order.category
                      }}</span>
                    </h5>
                  </div>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
    </div>
  </div>
  <aside class="sidebar" v-if="!$helper.empty(selectedOrder)">
    <div class="sidebar__inner scrollable-content pt-2 px-2">
      <div
        class="sidebar__stuck align-item-center d-flex py-2 border-bottom mb-2"
      >
        <h5 class="fw-bolder mb-0">
          Order No#{{ $helper.generateVoucherNo(selectedOrder.id) }}
        </h5>
        <div class="ms-auto">
          <div class="dropdown d-inline-block">
            <a
              href="javascript:void(0)"
              class="btn btn-xs btn-primary"
              title="Credit Post"
              data-bs-toggle="dropdown"
              @click="getClients"
              ><i class="demo-pli-credit-card-2 fw-bold icon fs-5"></i
            ></a>
            <div
              class="dropdown-menu dropdown-menu-end w-md-250px w-sm-250px"
              data-popper-placement="bottom-end"
            >
              <div class="">
                <h4>Credit Post</h4>
                <loading-dots v-if="!clients.length" />
                <template v-else>
                  <div class="form-group mb-3">
                    <select
                      class="form-control form-control-lg form-select"
                      v-model="client"
                    >
                      <option value="null" hidden disabled>
                        Select Customer
                      </option>
                      <option
                        :value="client"
                        v-for="client in clients"
                        :key="client.id"
                      >
                        {{ client.name }} [{{ client.type }}]
                      </option>
                    </select>
                  </div>
                  <div class="form-group">
                    <wolf-button
                      class="w-100 btn-lg btn btn-primary"
                      activator="ASSIGNING_ORDER"
                      :disabler="$helper.empty(client)"
                      @clicked="assignClient"
                    >
                      <span>POST</span>
                    </wolf-button>
                  </div>
                </template>
              </div>
            </div>
          </div>
          <a
            href="javascript:void(0)"
            class="btn btn-xs btn-danger ms-1"
            @click.prevent="
              $confirm({
                title: 'Delete Order',
                content: `Are you sure you want to delete this order #${$helper.generateVoucherNo(
                  selectedOrder.id
                )} ?`,
                classes: 'btn btn-danger',
                actionText: 'Yes, Continue',
                closeText: 'Cancel',
                action: () => {
                  handleDestroy(selectedOrder.id);
                },
              })
            "
          >
            <i class="demo-pli-trash fw-bold icon fs-5"></i>
          </a>
          <a
            href="javascript:void(0)"
            class="text-primary ms-1"
            @click="closeSidebar"
            ><i class="demo-pli-cross fw-bold icon fs-5"></i
          ></a>
        </div>
      </div>

      <div class="sidebar__wrap">
        <div class="pos-cart scrollable-content">
          <div id="items-container">
            <div class="selected-order-items">
              <div
                class="item mb-2"
                v-for="(item, i) in selectedOrder.items"
                :key="'order_items' + i"
              >
                <h5 class="fw-bolder mb-0">{{ item.name }}</h5>
                <p class="fw-normal m-0"></p>
                <div class="mt-1 d-flex flex-nowrap align-items-center">
                  <div
                    class="unit-price w-25 d-flex flex-nowrap align-items-center"
                  >
                    <span class="h6 m-0">{{ formatMoney(item.price) }}</span>
                    <span class="text-muted ms-auto">x{{ item.quantity }}</span>
                  </div>
                  <div class="total ms-auto text-end">
                    <span class="h5 mb-0 fw-bolder">{{
                      formatMoney(item.amount)
                    }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div
            class="position-sticky w-100 bottom-0 bg-white shadow-lg pt-1 border-top"
            style="z-index: 9999"
          >
            <div class="d-flex align-items-center mt-2">
              <span>Total</span>
              <span class="h5 fw-bolder mb-0 ms-auto">{{
                formatMoney(selectedOrder.grand_total)
              }}</span>
            </div>
            <div class="payments" v-if="selectedOrder.printed == 1">
              <h5 class="mb-1 fw-bolder">Payment Methods</h5>
              <div class="d-inline-block py-2">
                <label
                  class="py-0 px-2 mb-1 rounded-pill bg-light border rounded-2 ms-1 cursor-pointer"
                  :class="{
                    'active-payment': paymentMethod == payment,
                  }"
                  :for="'payment' + i"
                  v-for="(payment, i) in paymentMethods"
                  :key="'payment' + i"
                >
                  <span class="">{{ payment }}</span>
                  <input
                    :id="'payment' + i"
                    type="radio"
                    class="d-none"
                    v-model="paymentMethod"
                    :value="payment"
                  />
                </label>
              </div>
              <wolf-button
                class="bnt-block btn btn-primary w-100"
                activator="PAYING_BILL"
                :disabler="submitDisabled"
                @clicked="handlePayment"
              >
                Pay Now
              </wolf-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </aside>
</template>
