<script>
import Avatar from "@/components/Avatar.vue";
import LazyImage from "@/components/LazyImage.vue";
import { Swiper, SwiperSlide } from "swiper/vue";
import EmptyResults from "@/components/EmptyResults.vue";
import { Navigation } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
export default {
  name: "OrderCreator",
  components: {
    Avatar,
    Swiper,
    SwiperSlide,
    LazyImage,
    EmptyResults,
  },
  data: () => ({
    types: [],
    items: [],
    itemsFilter: {
      group: "all",
      category: null,
    },
    modules: [Navigation],
    searchKey: null,
    orderItems: [],
    tables: [],
    selectedTable: {},
    tableKeyword: null,
    orderCategory: "DINE IN",
    client: null,
    comment: null,
    searchBarOpen: false,
    record: null,
    lockedTables: [],
    checkTableInterval: null,
  }),
  computed: {
    categories() {
      let records = this.types;
      if (this.itemsFilter.group != "all") {
        let filter = this.itemsFilter.group == "drinks" ? "BAR" : "KITCHEN";
        records = records.filter((row) => {
          return row.type == filter;
        });
      }
      this.itemsFilter.category = null;
      return records;
    },
    products() {
      let searchKey = this.searchKey && this.searchKey.toLowerCase();
      let rows = this.items;
      if (searchKey) {
        rows = rows.filter(
          (row) => String(row.name).toLowerCase().indexOf(searchKey) > -1
        );
      } else {
        if (!this.$helper.empty(this.itemsFilter.category)) {
          let id = this.itemsFilter.category.id;
          rows = rows.filter((row) => {
            return row.type_id == id;
          });
        }
        if (this.itemsFilter.group != "all") {
          let filter = this.itemsFilter.group == "drinks" ? "BAR" : "KITCHEN";
          rows = rows.filter((row) => {
            return row.group == filter;
          });
        }
      }
      return rows;
    },
    cartItems() {
      return this.orderItems.map((item) => item.id);
    },
    subTotal() {
      return this.orderItems.reduce(
        (a, b) => a + b.quantity * (b.price || b.cost),
        0
      );
    },
    filteredTables() {
      let keyword = this.tableKeyword && this.tableKeyword.toLowerCase();
      let tables = this.tables;
      if (keyword) {
        tables = tables.filter(
          (item) => String(item.name).toLowerCase().indexOf(keyword) > -1
        );
      }
      return tables;
    },
    existingItems() {
      return this.orderItems.filter(
        (item) => item.old_quantity || item.round_key
      );
    },
    submitDisabled() {
      return (
        (this.record !== null &&
          this.existingItems.every(
            (item) => item.old_quantity == item.quantity
          ) &&
          this.existingItems.length == this.orderItems.length) ||
        this.$helper.empty(this.selectedTable) ||
        !this.cartItems.length
      );
    },
  },
  beforeCreate() {
    this.$http.get("items/types").then((response) => {
      this.types = response.data.rows;
    });
  },
  created() {
    let reference = this.$route.params.reference;
    if (reference) {
      this.$http.get(`orders/items/${reference}`)
        .then((response) => {
          this.record = response.data.record;
          this.orderItems = [...response.data.items, ...this.orderItems];
          this.selectedTable = response.data.record.table;
        });
    }
  },
  mounted() {
    this.$http.get("items/list?nopagination=1")
      .then((response) => {
        this.items = response.data.rows;
      });
    this.$http.get("tables/show").then((response) => {
      this.tables = response.data.tables;
    });
    this.checkTableInterval = setInterval(() => {
      this.$store.commit("SET_REQUEST_FLAG", "GETTING_LOCKED_TABLES");
      this.$http.get(`tables/locked`).then((response) => {
        if (response.data.result) {
          this.lockedTables =
            (response.data.result.tables &&
              response.data.result.tables
                .split(",")
                .map((item) => Number(item))) ||
            [];
        }
      });
    }, 3000);
  },
  methods: {
    chooseTable(table) {
      if (this.lockedTables.includes(table.id))
        this.$store.commit("SET_FLASH_MESSAGE", {
          type: "danger",
          message: `Table ${
            table.name || table.id
          } is currently locked on a pending invoice`,
        });
      else this.selectedTable = table;
    },
    addToCart(item, sign = "+") {
      if (this.cartItems.includes(item.id)) {
        let index = this.cartItems.indexOf(item.id);
        if (sign == "-") {
          this.orderItems[index].quantity -= 1;
          if (this.orderItems[index].quantity <= 0)
            this.orderItems.splice(index, 1);
        } else this.orderItems[index].quantity += 1;
      } else {
        item.quantity = 1;
        this.orderItems.push(item);
      }
    },
    editItemCart(item, sign = "+") {
      let index = this.cartItems.indexOf(item.id);
      if (sign == "-") {
        if (item.quantity > item.old_quantity)
          this.orderItems[index].quantity -= 1;
      } else {
        this.orderItems[index].quantity += 1;
        this.orderItems[index].edited = true;
      }
    },
    removeOrderItem(item) {
      this.$confirm({
        title: "Delete Item from orders",
        content: `Are you sure you want to remove ${item.name}`,
        classes: "btn btn-danger",
        actionText: "Continue",
        closeText: "Discard",
        action: () => {
          let index = this.cartItems.indexOf(item.id);
          this.orderItems[index].edited = true;
        },
      });
    },
    getGeneralName(categoryName) {
      let result = categoryName.split("/")[1] || categoryName;
      return result.toLowerCase();
    },
    setCurrentCategory(item) {
      this.itemsFilter.category = item;
    },
    handleSubmit() {
      let barItems = [];
      let kitchenItems = [];
      let items = this.orderItems.filter(
        (item) =>
          (item.round_key && item.old_quantity != item.quantity) ||
          !item.round_key
      );
      items.forEach((item) => {
        if (item.group == "BAR") barItems.push(item);
        else kitchenItems.push(item);
      });

      this.$http
        .post(
          "orders/store",
          this.$helper.generateFormData({
            category: this.orderCategory,
            table_id: this.selectedTable.id,
            client_id: this.client,
            comment: this.comment,
            grand_total: this.subTotal,
            discount: 0,
            amount: this.subTotal,
            bar_items: barItems,
            kitchen_items: kitchenItems,
            order_id: this.record != null ? this.record.id : null,
          })
        )
        .then((response) => {
          if (response.data.status) {
            this.$store.commit("SET_FLASH_MESSAGE", {
              type: "success",
              message: "Order placed successfully",
            });
            if (this.record == null) this.handleSignoutAction();
            else this.$router.replace({ name: "WaiterOrders" });
          }
        });
    },
    handleSignoutAction() {
      this.$http
        .post("auth/logout", this.$helper.generateFormData({}))
        .then((response) => {
          if (response.data.status) {
            this.$store.dispatch("logout").then(() => {
              window.location.replace(
                this.$router.resolve({ name: "FrontOfficeLogin" }).href
              );
            });
          }
        });
    },
    hideSearchBar() {
      this.searchBarOpen = false;
      this.searchKey = null;
    },
    showSearch() {
      this.searchBarOpen = true;
      this.$nextTick(() => {
        document.querySelector("#search-field").focus();
      });
    },
  },
  beforeUnmount() {
    clearInterval(this.checkTableInterval);
  },
};
</script>
